import React from 'react'
import { VictoryChart, VictoryGroup, VictoryLine, VictoryArea, VictoryLabel } from 'victory'
import { histPercentiles, smooth } from './queue'

/* Styling */
const BLUE = '#4198c8'; // official MGGG blue
const RED = '#d1352b';  // red from Districtr
const GREEN = '#87ca3f'; // lightish green modified from Districtr

// percentile bands with alpha values
const PERCENTILES = [1, 5, 20, 50, 80, 95, 99];
const BAND_ALPHAS = [0.2, 0.35, 0.52, 0.52, 0.35, 0.2];

const SMOOTH_WINDOW = 15;

class WaitTimeChart extends React.Component {

    render() {
        let peoplePercentileBands = [];
        let peopleMedianXY = [];
        let offset = Math.floor(SMOOTH_WINDOW / 2);
        if (this.props.separateStudentsStaff === false &&
            this.props.peopleHists !== undefined) {
            let peoplePercentiles = this.props.peopleHists.map(h => histPercentiles(h, PERCENTILES));
            let peoplePercentilesSmoothed = Array(PERCENTILES.length);
            for (let idx = 0; idx < PERCENTILES.length; idx++) {
                let percentileData = peoplePercentiles.map(p => p[idx]);
                let smoothed = smooth(percentileData, SMOOTH_WINDOW);
                peoplePercentilesSmoothed[idx] = smoothed;
                if (PERCENTILES[idx] === 50) {
                    for (let ts = 0; ts < smoothed.length; ts++) {
                        peopleMedianXY.push({'x': ts + offset, 'y': smoothed[ts]});
                    }
                }
            }
            for (let idx = 0; idx < BAND_ALPHAS.length; idx++) {
                let y0 = peoplePercentilesSmoothed[idx];
                let y = peoplePercentilesSmoothed[idx + 1];
                let xy = Array(y.length);
                for (let ts = 0; ts < y.length; ts++) {
                    xy[ts] = {'x': ts + offset, 'y': y[ts], 'y0': y0[ts]};
                }
                peoplePercentileBands.push({'xy': xy, 'alpha': BAND_ALPHAS[idx]});
            }
        }

        return (
            <VictoryChart width={800} height={400}>
                <VictoryGroup
                  style={{
                    data: { strokeWidth: 3.5 }
                  }}
                >
                  <VictoryLine
                    style={{
                      data: { stroke: BLUE }
                    }}
                    data={peopleMedianXY}
                  />
                </VictoryGroup>
                {peoplePercentileBands.map(xy => {
                    return (
                        <VictoryGroup
                          style={{
                            data: { strokeWidth: 0, fillOpacity: xy['alpha'] }
                          }}
                        >
                          <VictoryArea
                            style={{
                              data: { fill: BLUE, stroke: BLUE }
                            }}
                            data={xy['xy']}
                          />
                        </VictoryGroup>
                    );
                })}
                <VictoryLabel x={355} y={388} text="Arrival time" />
                <VictoryLabel x={5} y={235} angle={270} text="Wait time (minutes)" />
            </VictoryChart>
        );
    }
}

export default WaitTimeChart;


